@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);

.lds-facebook{display:inline-block;position:relative;width:80px;height:80px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:#f1b722;-webkit-animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.lds-facebook div:nth-child(1){left:8px;-webkit-animation-delay:-0.24s;animation-delay:-0.24s}.lds-facebook div:nth-child(2){left:32px;-webkit-animation-delay:-0.12s;animation-delay:-0.12s}.lds-facebook div:nth-child(3){left:56px;-webkit-animation-delay:0;animation-delay:0}@-webkit-keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}
